age3 .game {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  background-image: linear-gradient(-180deg, #611affcc 0%, #1b62ffcc 100%);\n  position: relative;\n}\n\n.memoryCard .stage3 .game .cardsContainer {\n  box-sizing: border-box;\n  width: 100%;\n  height: 100%;\n  padding: 2.666667rem 0.133333rem 0.266667rem;\n}\n\n.memoryCard .stage3 .game .cardsContainer .cards {\n  box-sizing: border-box;\n  width: 100%;\n  height: 100%;\n  padding: 0.266667rem 0.133333rem;\n  background-image: url('./src/assets/memorycard/14.png');\n  background-size: 100% 100%;\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: space-evenly;\n}\n\n.memoryCard .stage3 .game .cardsContainer .cards li {\n  position: relative;\n}\n\n.memoryCard .stage3 .game .cardsContainer .cards li .front {\n  box-sizing: border-box;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: space-around;\n  background: #000000;\n  border: 0.026667rem solid #00F4EF;\n}\n\n.memoryCard .stage3 .game .cardsContainer .cards li .front img {\n  width: 80%;\n}\n\n.memoryCard .stage3 .game .cardsContainer .cards li .front p {\n  font-family: PingFangSC-Regular;\n  font-size: 0.24rem;\n  line-height: 0.4rem;\n  color: #fff;\n  text-align: center;\n}\n\n.memoryCard .stage3 .game .header {\n  box-sizing: border-box;\n  width: 100%;\n  padding: 0.4rem 0.266667rem 0.4rem;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  position: absolute;\n  left: 0;\n  top: 0;\n}\n\n.memoryCard .stage3 .game .header img {\n  width: 1.6rem;\n  height: 1.6rem;\n}\n\n.memoryCard .stage3 .game .progress {\n  width: 4rem;\n  height: 0.733333rem;\n  border: 0.026667rem solid #FFFFFF;\n  border-radius: 0.133333rem;\n  box-shadow: 0 0 0.053333rem 0 #7ACFE3;\n  background: #06F9F1;\n}\n\n.memoryCard .stage3 .game .progress.overBkg {\n  background: grey;\n}\n\n.memoryCard .stage3 .game .progress .progressInner {\n  height: 100%;\n  border-top-left-radius: 0.133333rem;\n  border-bottom-left-radius: 0.133333rem;\n  background-color: #fff;\n}\n\n.memoryCard .stage3 .game .progress .progressInner.active {\n  border-top-right-radius: 0.133333rem;\n  border-bottom-right-radius: 0.133333rem;\n}\n\n.memoryCard .stage3 .game .timer {\n  font-family: PingFangSC-Regular;\n  width: 2rem;\n  color: #06F9F1;\n  font-size: 0.96rem;\n  line-height: 0.96rem;\n  text-align: right;\n}\n\n.memoryCard .stage3 .game .timer .over {\n  font-size: 0.666667rem;\n}\n\n.memoryCard .stage3 .playResult,\n.memoryCard .stage3 .lotteryResult {\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.6);\n  position: fixed;\n  left: 0;\n  top: 0;\n}\n\n.memoryCard .stage3 .lotteryResult {\n  padding-top: 1.333333rem;\n  text-align: center;\n}\n\n.memoryCard .stage3 .lotteryResult .lotteryTitle {\n  width: 60%;\n}\n\n.memoryCard .stage3 .lotteryResult .prizeItem {\n  width: 9.333333rem;\n  height: 9.2rem;\n  margin: 0 auto;\n}\n\n.memoryCard .stage3 .lotteryResult .introBtn {\n  width: 4.053333rem;\n  height: 1.066667rem;\n  border: 0.026667rem solid #fff;\n  border-radius: 0.506667rem;\n  margin: 0 auto 0.666667rem;\n  font-family: PingFangSC-Regular;\n  color: #fff;\n  font-size: 0.426667rem;\n  line-height: 1.066667rem;\n  text-align: center;\n}\n\n.memoryCard .stage3 .playResult p {\n  margin-bottom: 1.306667rem;\n  font-family: PingFangSC-Semibold;\n  font-size: 0.4rem;\n  line-height: 0.56rem;\n  color: #fff;\n  letter-spacing: 0;\n  text-align: center;\n}\n\n.memoryCard .stage3 .playResult .lotteryBtn,\n.memoryCard .stage3 .playResult .playBtn,\n.memoryCard .stage3 .lotteryResult .playBtn {\n  width: 4.053333rem;\n  height: 1.066667rem;\n  margin: 0 auto 0.533333rem;\n}\n\n.memoryCard .stage3 .playResult .closeBtn {\n  right: 0.426667rem;\n  top: 2.186667rem;\n}\n\n/*第一幕*/\n\n.memoryCard .stage1 .flipBtn {\n  width: 2.133333rem;\n  height: 2.133333rem;\n  margin-left: -1.066667rem;\n  position: absolute;\n  left: 50%;\n  top: 51%;\n}\n\n@media screen and (max-height: 600px) {\n  .memoryCard .stage1 .flipBtn {\n    top: 50%;\n  }\n}\n\n/*iphone x*/\n\n@media only screen\nand (device-width : 375px)\nand (device-height : 812px)\nand (-webkit-device-pixel-ratio : 3) {\n  .memoryCard .stage1 .flipBtn {\n    top: 42%;\n  }\n}\n\n.memoryCard .stage1 .rulesBtn {\n  padding-left: 0.266667rem;\n  height: 0.586667rem;\n  border-radius: 0.293333rem 0 0 0.293333rem;\n  background-image: linear-gradient(-180deg, #FF7B5F 0%, #FF9649 100%);\n  font-family: PingFangSC-Semibold;\n  color: #fff;\n  font-size: 0.4rem;\n  line-height: 0.586667rem;\n  text-align: center;\n  position: absolute;\n  right: 0;\n  top: 0.506667rem;\n}\n\n.memoryCard .stage1 .rules {\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.6);\n  position: fixed;\n  left: 0;\n  top: 0;\n}\n\n.memoryCard .stage1 .rules .container {\n  width: 9.146667rem;\n  height: 11.76rem;\n  border-radius: 0.266667rem;\n  background-image: url('./src/assets/memorycard/4.png');\n  background-size: 100% 100%;\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  margin-left: -4.573333rem;\n  margin-top: -5.88rem;\n}\n\n.memoryCard .stage1 .rules .container .title {\n  width: 100%;\n  height: 1.333333rem;\n  border-bottom: 0.013333rem solid #e7e7e780;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.memoryCard .stage1 .rules .container .title img {\n  width: 1.786667rem;\n  height: 0.853333rem;\n}\n\n.memoryCard .stage1 .rules .container p {\n  padding: 0.24rem 0 0.533333rem;\n  font-family: PingFangSC-Medium;\n  color: #fff;\n  font-size: 0.373333rem;\n  line-height: 0.48rem;\n  text-align: center;\n}\n\n.memoryCard .stage1 .rules .container ul {\n  width: 8.72rem;\n  margin: 0 auto;\n}\n\n.memoryCard .stage1 .rules .container ul li {\n  box-sizing: border-box;\n  display: flex;\n  width: 8.72rem;\n  height: 2.466667rem;\n  padding: 0.293333rem 0.32rem;\n  border-radius: 0.133333rem;\n  margin-bottom: 0.213333rem;\n  background-color: #fff;\n  position: relative;\n}\n\n.memoryCard .stage1 .rules .container ul li .introLeft {\n  width: 1.893333rem;\n  height: 1.893333rem;\n  border-radius: 0.133333rem;\n  margin-right: 0.4rem;\n  position: relative;\n}\n\n.memoryCard .stage1 .rules .container ul li .introLeft .name {\n  width: 100%;\n  height: 0.533333rem;\n  border-radius: 0 0 0.133333rem 0.133333rem;\n  background: rgba(0, 0, 0, 0.35);\n  font-family: PingFangSC-Medium;\n  color: #fff;\n  font-size: 0.373333rem;\n  line-height: 0.533333rem;\n  text-align: center;\n  position: absolute;\n  left: 0;\n  bottom: 0;\n}\n\n.memoryCard .stage1 .rules .container ul li .introRight {\n  width: 5.786667rem;\n}\n\n.memoryCard .stage1 .rules .container ul li .introRight span {\n  font-family: PingFangSC-Medium;\n  color: #424242;\n  font-size: 0.373333rem;\n  line-height: 0.533333rem;\n  text-align: center;\n}\n\n.memoryCard .stage1 .rules .container ul li .introRight div {\n  width: 1.906667rem;\n  height: 0.946667rem;\n  position: absolute;\n  right: 0.426667rem;\n  bottom: 0.28rem;\n}\n\n.memoryCard .stage1 .rules .container ul li .introRight div img {\n  width: 100%;\n  height: 100%;\n}\n\n.memoryCard .closeBtn {\n  width: 0.64rem;\n  height: 0.64rem;\n  position: absolute;\n}\n\n.memoryCard .stage1 .rules .container .closeBtn {\n  right: 0;\n  top: -0.84rem;\n}\n\n.memoryCard .stage1 .tip {\n  width: 2.933333rem;\n  height: 1.866667rem;\n  border-radius: 0.133333rem;\n  background: rgba(0, 0, 0, 0.6);\n  font-family: PingFangSC-Medium;\n  font-size: 0.373333rem;\n  color: #fff;\n  text-align: center;\n  line-height: 1.866667rem;\n  position: fixed;\n  left: 50%;\n  top: 50%;\n  margin-left: -1.466667rem;\n  margin-top: -0.933333rem;\n}"],"sourceRoot":""}]);

