  <div class="memoryCard">
    <!-- 第一幕 抽奖按钮-->
    <div class="stage stage1" v-show="stage === 1">
      <img src="../assets/memorycard/1.png">
      <div class="flipBtn" @click="gotoFlip">
        <img src="../assets/memorycard/2.png">
      </div>
      <!-- 奖品规则按钮 -->
      <div class="rulesBtn" @click="showRules">奖品规则</div>
      <!-- 奖品规则详情 -->
      <div class="rules" v-show="rulesShow">
        <div class="container">
          <div class="title"><img src="../assets/memorycard/16.png"></div>
          <p>选中进行翻盘，若牌中图案一致，则完成消除。<br>
            在{{timer}}秒内清除完全部牌面获得抽奖机会！</p>
          <ul>
            <li v-for="item in prizeList">
              <div class="introLeft">
                <img src="../assets/memorycard/2.png">
                <div class="name">{{item.alias}}</div>
              </div>
              <div class="introRight">
                <span>{{item.name}} 中奖概率{{item.probability}}%</span>
                <div @click="startFlip"><img src="../assets/memorycard/15.png"></div>
              </div>
            </li>
          </ul>
          <div class="closeBtn" @click="startFlip"><img src="../assets/memorycard/6.png"></div>
        </div>
      </div>
      <!-- 扣减积分提示 -->
      <div class="tip" v-show="tipShow">{{tip}}</div>
    </div>
    <!-- 第二幕 gif ready go -->
    <div class="stage stage2" v-show="stage === 2">
      <img src="../assets/memorycard/3.gif">
    </div>
    <!-- 第三幕 玩游戏 及 结果页 -->
    <div class="stage stage3" v-show="stage === 3">
      <!-- 游戏部分 -->
      <div class="game">
        <div class="header">
          <img src="../assets/memorycard/12.png">
          <div class="progress"
            :class="{'overBkg': isOver}">
            <div class="progressInner"
              :class="{'active': timerLeft === 0}"
              :style="{width: percent}"></div></div>
          <div class="timer">
            <span v-if="!isOver">{{timerLeft}}S</span>
            <span class="over" v-else>OVER</span>
          </div>
        </div>
        <div class="cardsContainer">
          <ul class="cards" ref="cardList">
            <li v-for="(item, index) in cards"
              :style="{width: cardWidth + 'px', height: cardHeight + 'px'}">
              <div class="flip-container"
                v-show="item.show"
                @click="flipCard(index)">
                <div class="flipper"
                  :class="{'flipped': item.flipped}"
                  :style="{width: cardWidth + 'px', height: cardHeight + 'px'}">
                  <div class="front">
                    <img :src="require('../assets/memorycard/signs/' + item.en + '.png')">
                    <p>{{item.zh_CN}}</p>
                  </div>
                  <div class="back">
                    <img src="../assets/memorycard/13.png">
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- 结果部分 -->
      <!-- 挑战成功/失败 -->
      <div class="playResult" v-show="playResultShow">
        <img src="../assets/memorycard/7.png" v-show="playWin">
        <img src="../assets/memorycard/8.png" v-show="!playWin">
        <p>{{playWin ? '获得一次抽奖机会' : '在60秒内消除全部牌面才可以参加抽奖哦！'}}</p>
        <div class="lotteryBtn"
          @click="gotoLottery"
          v-show="playWin">
          <img src="../assets/memorycard/5.png">
        </div>
        <div class="playBtn"
          @click="playAgain"><img src="../assets/memorycard/9.png"></div>
        <!-- <div class="closeBtn" @click="closePlayResult"><img src="../assets/memorycard/6.png"></div> -->
      </div>
      <!-- 抽奖成功/失败 -->
      <div class="lotteryResult" v-show="lotteryResultShow">
        <img class="lotteryTitle" src="../assets/memorycard/17.png"
          v-show="lotteryWin">
        <img class="lotteryTitle" src="../assets/memorycard/18.png"
          v-show="!lotteryWin">
        <div class="prizeItem" v-show="lotteryWin">
          <img src="../assets/memorycard/11.png">
        </div>
        <div class="introBtn"
          @click="readIntro"
          v-show="lotteryWin">查看使用说明</div>
        <div class="playBtn"
          :style="{marginTop: !lotteryWin ? '240px' : '0px'}"
          @click="playAgain"><img src="../assets/memorycard/9.png"></div>
      </div>
    </div>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      stage: 1,
      // 第一幕
      prizeList: [{
        name: 'dyson吸尘器',
        probability: 10,
        pic: '',
        alias: '奖品一'
      },
      {
        name: '苹果',
        probability: 20,
        pic: '',
        alias: '奖品二'
      },
      {
        name: '橘子',
        probability: 60,
        pic: '',
        alias: '奖品三'
      }],
      rulesShow: false, // 奖品规则显示
      tipShow: false, // 扣除积分显示
      tip: '', // 扣除积分提示语
      // 第三幕 游戏部分
      cardWidth: 0, // 卡牌的宽高
      cardHeight: 0,
      // 十二星座卡牌
      signs: [{ en: 'capricorn', zh_CN: '魔羯座' },
        { en: 'aquarius', zh_CN: '水瓶座' },
        { en: 'pisces', zh_CN: '双鱼座' },
        { en: 'aries', zh_CN: '白羊座' },
        { en: 'taurus', zh_CN: '金牛座' },
        { en: 'gemini', zh_CN: '双子座' },
        { en: 'cancer', zh_CN: '巨蟹座' },
        { en: 'leo', zh_CN: '狮子座' },
        { en: 'virgo', zh_CN: '处女座' },
        { en: 'libra', zh_CN: '天秤座' },
        { en: 'scorpio', zh_CN: '天蝎座' },
        { en: 'sagittarius', zh_CN: '射手座' }],
      timer: 60, // 总共玩多少秒
      timerLeft: 60, // 还剩多少秒
      intervalId: null, // 计时返回
      cards: [], // 生成的随机12张卡牌 {en: '', flipped: false, show: true, zh_CN: ''}
      arrFliped: [], // 已经被翻过的卡牌push进去 {en: en, index: index}
      playAble: false, // 开关：是否可以点击卡牌
      memoryTime: 5000, // 给你多少秒后记忆卡牌顺序
      flippedCount: 0, // 消失一对就+1，当等于6时说明全部消除，游戏成功
      isOver: false, // 游戏是否结束
      // 游戏结果
      playResultShow: false,
      // 抽奖结果
      lotteryResultShow: false,
      lotteryWin: false
    }
  },
  computed: {
    percent: function () {
      return (1 - (this.timerLeft / this.timer).toFixed(2)) * 100 + '%'
    },
    playWin: function () {
      return this.flippedCount === 6
    }
  },
  watch: {
    stage: function (newVal) {
      switch (newVal) {
        case 2:
          let that = this
          window.setTimeout(function () {
            that.stage ++
          }, 5000)
          break
        case 3:
          this.resetGame()
          break
        default:
          return false
      }
    },
    playWin: function (newVal) {
      if (newVal) {
        this.stopGame()
      }
    }
  },
  mounted () {
    this.setCardWidthHeight()
    // this.resetGame()
  },
  methods: {
    // 第三幕
    // 设置卡牌宽高
    setCardWidthHeight: function () {
      // 图片宽高比例 212 272
      this.cardWidth = parseInt((document.body.clientWidth - 40) * 0.85 / 3)
      this.cardHeight = parseInt(this.cardWidth / 212 * 272)
    },
    resetGame: function () {
      // 游戏玩失败重新玩 计时还在继续
      this.playAble = false
      this.arrFliped = []
      this.flippedCount = 0
      this.cards = this.getRandomCards()
      let that = this
      window.setTimeout(function () {
        _.each(that.cards, function (card) {
          card.flipped = true // true 显示背面
        })
        that.playAble = true
        that.startCountdown()
      }, that.memoryTime)
    },
    playAgain: function () {
      // 需要扣减积分
      // 重新玩，重新计时
      this.timerLeft = 60
      this.isOver = false
      this.playResultShow = false
      this.resetGame()
    },
    flipCard: function (index) {
      if (!this.playAble) {
        console.log('还不能玩游戏')
        return false
      }
      // 点击了先把牌反过来
      let item = this.cards[index]
      item.flipped = false // false 显示正面
      this.$set(this.cards, index, item)

      let len = this.arrFliped.length
      // === 0 说明之前没点过
      if (len === 0) {
        let en = this.cards[index].en
        this.arrFliped.push({en: en, index: index})
      } else if (len === 1) {
        let en = this.cards[index].en
        let enA = this.arrFliped[0].en
        let idx = this.arrFliped[0].index
        if (idx !== index) {
          if (en === enA) {
            console.error('选中了 index 消失')
            this.playAble = false
            this.flippedCount ++
            let that = this
            window.setTimeout(function () {
              that._disappearSameCards(index)
            }, 500)
          } else {
            console.error('选错了，游戏重新玩')
            this.playAble = false
            let that = this
            // 1s后卡牌翻回背面
            window.setTimeout(function () {
              that._flipCardsBack(index)
            }, 1000)
            // 2s后重玩游戏
            window.setTimeout(function () {
              window.clearInterval(that.intervalId)
              that.resetGame()
            }, 2000)
          }
        } else {
          console.error('重复点击同一张卡牌了')
        }
      }
    },
    _disappearSameCards: function (index) {
      let idxA = this.arrFliped[0].index
      let idxB = index
      let itemA = this.cards[idxA]
      itemA.show = false
      let itemB = this.cards[idxB]
      itemB.show = false
      this.$set(this.cards, idxA, itemA)
      this.$set(this.cards, idxB, itemB)
      this.playAble = true
      this.arrFliped = []
    },
    _flipCardsBack: function (index) {
      let idxA = this.arrFliped[0].index
      let idxB = index
      let itemA = this.cards[idxA]
      itemA.flipped = true
      let itemB = this.cards[idxB]
      itemB.flipped = true
      this.$set(this.cards, idxA, itemA)
      this.$set(this.cards, idxB, itemB)
    },
    // 倒计时逻辑
    startCountdown: function () {
      console.log('开始计时')
      let that = this
      this.intervalId = window.setInterval(function () {
        if (that.timerLeft > 0) {
          that.timerLeft --
        } else {
          that.stopGame()
        }
      }, 1000)
    },
    stopGame: function () {
      console.log('游戏结束')
      let that = this
      window.clearInterval(that.intervalId)
      this.isOver = true
      this.playResultShow = true
      this.playAble = false
    },
    // 生成随机6组卡牌，每组2个 并且排列随机
    getRandomCards: function () {
      let arrIndex = this._getRandomIndxArr(6)
      let arrA = []
      for (let i = 0; i < arrIndex.length; i++) {
        let idx = arrIndex[i]
        let item = this.signs[idx]
        item.flipped = false
        item.show = true
        arrA.push(item)
      }
      let arrX = [].concat(_.cloneDeep(arrA), _.cloneDeep(arrA))
      return _.shuffle(arrX)
    },
    _getRandomIndxArr: function (count) {
      let arr = []
      for (let i = 0; i < count; i++) {
        let num = this._getRandomIndex(arr, this.signs.length - 1)
        arr.push(num)
      }
      return arr
    },
    _getRandomIndex (arr, max) {
      let num = _.random(0, max)
      if (arr.indexOf(num) === -1) {
        return num
      } else {
        return this._getRandomIndex(arr, max)
      }
    },
    // closePlayResult: function () { this.playResultShow = false },
    // 去抽奖
    gotoLottery: function () {
      // 抽奖接口
    },
    // 查看奖品说明
    readIntro: function () {},
    // 第一幕
    // 点击翻牌按钮
    gotoFlip: function () {
      // 调用扣减积分接口
      this.minusPoint(true)
    },
    // 调用扣减积分接口的callback
    // result true 成功 false 失败
    // this.minusPoint(true)
    minusPoint: function (result) {
      this.tip = result ? '扣减积分成功！' : '扣减积分失败！'
      this.tipShow = true
      let that = this
      window.setTimeout(function () {
        that.tipShow = false
        if (result) {
          that.stage = 2
        }
      }, 500)
    },
    // 点击奖品规则
    showRules: function () { this.rulesShow = true },
    startFlip: function () { this.rulesShow = false }
  }
}
</script>
